@import url(https://fonts.googleapis.com/css2?family=Handlee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
*{
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    letter-spacing: 5px;
}

    body{
    background-color: #6bb9f6;
    } 

  .wrapper {
    height: 100%;
    width: 100%;
    background: linear-gradient(180deg, #04fafd, 5%, #119dff, 50%, #030423);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .main-container {
    font-size: 4vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    color: #fff;
    z-index: 998;
  }


  .main-container h1{
    background: #0b0f6e;
    padding: 1rem;
    border-radius: 1rem;
    font-size: 5vw;
    color: aqua;
    margin: 2rem;
    border: thick solid aqua;
  }

  .main-container h1:hover{
    cursor: pointer;
    border: solid thick white;
    color: white;
  }

  img{
    width: 10%;
  }

  p{
    color: rgba(241, 241, 241, 0.473);
    font-size: smaller;
  }


  /*  ----------- Modal Styling ----------*/

  .modal {
    height: 0;
    width:0;
    display: block;
    position: fixed;
    z-index: 999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
  }

  .modal.active {
    height: auto;
    width: 80%;
    opacity: 1;
  }


  .modal-content {
    font-family: 'Handlee', cursive;
    color: white;
    background-color: black;
    border-radius: 1rem;
    padding: 20px;
    width: 80%;
    max-width: 400px;
    position: relative;
    margin: 3% auto;
  }


  .close-button {
    color: white;
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
  }


  .selection{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .selection img{
    width: 70%;
    height: 12rem;
    margin: 2rem;
    border-radius: 1rem;
  }

  .selection img:hover{
    cursor: pointer;
    border: white solid 2px;
    opacity: 0.9;
  }

  label,p{
    font-size: large;
    letter-spacing: 0;
  }

  #label1{
    padding-bottom: 2rem;
    border-bottom: solid 1px grey;
  }



/* ------------ Bubbles styling -------------*/
  .wrapper .bubble {
    height: 60px;
    width: 60px;
    border: 2px solid rgba(255, 255, 255, 0.7);
    border-radius: 50px;
    position: absolute;
    top: 10%;
    left: 10%;
    -webkit-animation: 4s linear infinite;
            animation: 4s linear infinite;
  }
  div .dot {
    height: 10px;
    width: 10px;
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.5);
    position: absolute;
    top: 20%;
    right: 20%;
  }
  .wrapper .bubble:nth-child(1) {
    top: 20%;
    left: 20%;
    -webkit-animation: animate 8s linear infinite;
            animation: animate 8s linear infinite;
  }
  .wrapper .bubble:nth-child(2) {
    top: 60%;
    left: 80%;
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
  }
  .wrapper .bubble:nth-child(3) {
    top: 40%;
    left: 40%;
    -webkit-animation: animate 3s linear infinite;
            animation: animate 3s linear infinite;
  }
  .wrapper .bubble:nth-child(4) {
    top: 66%;
    left: 30%;
    -webkit-animation: animate 7s linear infinite;
            animation: animate 7s linear infinite;
  }
  .wrapper .bubble:nth-child(5) {
    top: 90%;
    left: 10%;
    -webkit-animation: animate 9s linear infinite;
            animation: animate 9s linear infinite;
  }
  .wrapper .bubble:nth-child(6) {
    top: 30%;
    left: 60%;
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
  }
  .wrapper .bubble:nth-child(7) {
    top: 70%;
    left: 20%;
    -webkit-animation: animate 8s linear infinite;
            animation: animate 8s linear infinite;
  }
  .wrapper .bubble:nth-child(8) {
    top: 75%;
    left: 60%;
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
  }
  .wrapper .bubble:nth-child(9) {
    top: 50%;
    left: 50%;
    -webkit-animation: animate 6s linear infinite;
            animation: animate 6s linear infinite;
  }
  .wrapper div:nth-child(10) {
    top: 45%;
    left: 20%;
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
  }
  .wrapper .bubble:nth-child(11) {
    top: 10%;
    left: 90%;
    -webkit-animation: animate 9s linear infinite;
            animation: animate 9s linear infinite;
  }
  .wrapper .bubble:nth-child(12) {
    top: 20%;
    left: 70%;
    -webkit-animation: animate 7s linear infinite;
            animation: animate 7s linear infinite;
  }
  .wrapper .bubble:nth-child(13) {
    top: 20%;
    left: 20%;
    -webkit-animation: animate 8s linear infinite;
            animation: animate 8s linear infinite;
  }
  .wrapper .bubble:nth-child(14) {
    top: 60%;
    left: 5%;
    -webkit-animation: animate 6s linear infinite;
            animation: animate 6s linear infinite;
  }
  .wrapper .bubble:nth-child(15) {
    top: 90%;
    left: 80%;
    -webkit-animation: animate 9s linear infinite;
            animation: animate 9s linear infinite;
  }
  @-webkit-keyframes animate {
    0% {
      -webkit-transform: scale(0) translateY(0) rotate(70deg);
              transform: scale(0) translateY(0) rotate(70deg);
    }
    100% {
      -webkit-transform: scale(1.3) translateY(-100px) rotate(360deg);
              transform: scale(1.3) translateY(-100px) rotate(360deg);
    }
  }
  @keyframes animate {
    0% {
      -webkit-transform: scale(0) translateY(0) rotate(70deg);
              transform: scale(0) translateY(0) rotate(70deg);
    }
    100% {
      -webkit-transform: scale(1.3) translateY(-100px) rotate(360deg);
              transform: scale(1.3) translateY(-100px) rotate(360deg);
    }
  }
  

* {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
}

.main-div {
    display: flex;
    flex-direction: column;
}

.above-surface {
    position: relative;
    height: 40vh;
    background: #acd4e4;
    z-index: 5;
}

.simulate-button{
    position: absolute;
    z-index: 999;
    margin: 1rem;
    padding: 1rem;
    width: auto;
    border: solid 2px;
    border-radius: 1rem;
    background-color: transparent;
    font-size: x-large;
    color:#101c27;
    border-color: #101c27;
    background-color: white;
}

.simulate-button:hover{
    cursor: pointer;
    color:white;
    border-color: white;
    background-color: #101c27;
    
}

.surface {
    background: url(data:image/png;base64,);
    height: 100px;
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 6;
}

.below-surface {
    position: relative;
    background: #6bb9f6;
    background: linear-gradient(to bottom, #6bb9f6, #2e6691, #2a4961, #101c27);
    height: auto;
    padding: 2vw;
}


.bars{
    position: fixed;
    right: 0;
    z-index: 999;
}

.plants-container{
    display: flex;
    justify-content: space-around;
    bottom: 0%;
}


.water-level{
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
}





/* ------------- waves and their animations -------------- */

#wave1{
    -webkit-animation: animate1 15s linear infinite;
            animation: animate1 15s linear infinite;
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
}
#wave2{
    -webkit-animation: animate1 30s linear infinite;
            animation: animate1 30s linear infinite;
    -webkit-animation-delay: -2s;
            animation-delay: -2s;
}
#wave3{
    -webkit-animation: animate2 15s linear infinite;
            animation: animate2 15s linear infinite;
    -webkit-animation-delay: -4s;
            animation-delay: -4s;
    opacity: 0.8;
}

@-webkit-keyframes animate1 {
    0%{
        background-position-x: 0;
        opacity: 0.2;
    }
    50%{
        opacity: 1;
    }
    100%{
        background-position-x: 1080px;
        opacity: 0.2;
    }
}

@keyframes animate1 {
    0%{
        background-position-x: 0;
        opacity: 0.2;
    }
    50%{
        opacity: 1;
    }
    100%{
        background-position-x: 1080px;
        opacity: 0.2;
    }
}
@-webkit-keyframes animate2 {
    0%{
        background-position-x: 1080px;
    }
    100%{
        background-position-x: 0px;
    }
}
@keyframes animate2 {
    0%{
        background-position-x: 1080px;
    }
    100%{
        background-position-x: 0px;
    }
}

/* ------------- phytos, fish, zooplanktons and their animation -------------*/
.phyto-img {
    width: 2vw;
    height: 2vh;
}




@-webkit-keyframes animate3 {
    0% {
        -webkit-transform: translate(4vw, 0) rotate(10deg);
                transform: translate(4vw, 0) rotate(10deg);
    }
    20% {
        -webkit-transform: translate(-4vw, -4vh);
                transform: translate(-4vw, -4vh);
    }
    40% {
        -webkit-transform: translate(-4vw, 4vh) rotate(30deg);
                transform: translate(-4vw, 4vh) rotate(30deg);
    }
    60% {
        -webkit-transform: translate(4vw, -4vh);
                transform: translate(4vw, -4vh);
    }
    80% {
        -webkit-transform: translate(4vw, 4vw) rotate(15deg);
                transform: translate(4vw, 4vw) rotate(15deg);
    }
    100%{
        -webkit-transform: translate(4vw, 0) rotate(10deg);
                transform: translate(4vw, 0) rotate(10deg);
        
    }
}




@keyframes animate3 {
    0% {
        -webkit-transform: translate(4vw, 0) rotate(10deg);
                transform: translate(4vw, 0) rotate(10deg);
    }
    20% {
        -webkit-transform: translate(-4vw, -4vh);
                transform: translate(-4vw, -4vh);
    }
    40% {
        -webkit-transform: translate(-4vw, 4vh) rotate(30deg);
                transform: translate(-4vw, 4vh) rotate(30deg);
    }
    60% {
        -webkit-transform: translate(4vw, -4vh);
                transform: translate(4vw, -4vh);
    }
    80% {
        -webkit-transform: translate(4vw, 4vw) rotate(15deg);
                transform: translate(4vw, 4vw) rotate(15deg);
    }
    100%{
        -webkit-transform: translate(4vw, 0) rotate(10deg);
                transform: translate(4vw, 0) rotate(10deg);
        
    }
}



@-webkit-keyframes animate4 {
    0%{
        -webkit-transform: translate(20vw) ;
                transform: translate(20vw) ;
    }

    50%{
        -webkit-transform: translate(0, 0) rotateY(180deg) ;
                transform: translate(0, 0) rotateY(180deg) ;
    }

    100%{
        -webkit-transform: translate(20vw) ;
                transform: translate(20vw) ;
    }

}



@keyframes animate4 {
    0%{
        -webkit-transform: translate(20vw) ;
                transform: translate(20vw) ;
    }

    50%{
        -webkit-transform: translate(0, 0) rotateY(180deg) ;
                transform: translate(0, 0) rotateY(180deg) ;
    }

    100%{
        -webkit-transform: translate(20vw) ;
                transform: translate(20vw) ;
    }

}


@-webkit-keyframes animate5 {
    0% { -webkit-transform: translateX(1vw) rotate(0deg); transform: translateX(1vw) rotate(0deg); }
    10% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    25% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
    30% { -webkit-transform: rotate(-12deg); transform: rotate(-12deg); }
    35% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
    40% { -webkit-transform: rotate(-12deg); transform: rotate(-12deg); }
    45% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
    50% { -webkit-transform: rotate(-12deg); transform: rotate(-12deg); }
    75% { -webkit-transform: rotate(12deg); transform: rotate(12deg) }
    100% { -webkit-transform: translateX(1vw) rotate(0deg); transform: translateX(1vw) rotate(0deg); }
  }


@keyframes animate5 {
    0% { -webkit-transform: translateX(1vw) rotate(0deg); transform: translateX(1vw) rotate(0deg); }
    10% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    25% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
    30% { -webkit-transform: rotate(-12deg); transform: rotate(-12deg); }
    35% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
    40% { -webkit-transform: rotate(-12deg); transform: rotate(-12deg); }
    45% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
    50% { -webkit-transform: rotate(-12deg); transform: rotate(-12deg); }
    75% { -webkit-transform: rotate(12deg); transform: rotate(12deg) }
    100% { -webkit-transform: translateX(1vw) rotate(0deg); transform: translateX(1vw) rotate(0deg); }
  }
  


